.style-toggler {
  position: absolute;
  left: -1px;
  background: #111;
  border: 1px solid #111;
  -webkit-box-shadow: 0 0 5px 1px #111111;
  -moz-box-shadow: 0 0 5px 1px #111111;
  box-shadow: 0 0 5px 1px #111111;
  -webkit-border-radius: 3px 3px 3px 3px;
  -moz-border-radius: 3px 3px 3px 3px;
  border-radius: 3px 3px 3px 3px;
  padding: 10px 0;
  top: 100px;
  z-index: 1050;
  cursor: pointer;
}
.style-switcher {
  background: #111;
  border: 1px solid #111;
  -webkit-box-shadow: 0 0 5px 1px #111111;
  -moz-box-shadow: 0 0 5px 1px #111111;
  box-shadow: 0 0 5px 1px #111111;
  -webkit-border-radius: 3px 3px 3px 3px;
  -moz-border-radius: 3px 3px 3px 3px;
  border-radius: 3px 3px 3px 3px;
  left: -230px;
  top: 100px;
  position: absolute;
  z-index: 1050;
  color: #eee;
  padding: 10px;
}
.style-switcher .pattern {
  list-style-type: none;
  margin: 15px 0;
}
.style-switcher .pattern li {
  margin-top: 8px;
  border: 1px solid #111;
}
.style-switcher .pattern li:first-child {
  margin-top: 0;
}
.style-switcher .pattern li a {
  display: block;
  padding: 8px 15px;
  color: #fff;
  -webkit-border-radius: 3px 3px 3px 3px;
  -moz-border-radius: 3px 3px 3px 3px;
  border-radius: 3px 3px 3px 3px;
}
.style-switcher .pattern li a.default {
  background: url('../img/bg2.png') repeat;
  color: #333;
}
.style-switcher .pattern li a.dark {
  background: url('../img/wood-black.jpg') repeat;
}
.style-switcher .pattern li a.light {
  background: url('../img/light.jpg') repeat;
  color: #333;
}
.style-switcher .pattern li a.wood {
  background: url('../img/wood.jpg') repeat;
  color: #333;
}
.style-switcher .pattern li a.retina-wood {
  background: url('../img/retina_wood.png') repeat;
  color: #333;
}
.style-switcher .pattern li a.linen {
  background: url('../img/low_contrast_linen.png') repeat;
}
.style-switcher .pattern li a.paper {
  background: url('../img/natural_paper.png') repeat;
  color: #333;
}
.style-switcher .color {
  list-style-type: none;
  margin: 10px 0;
}
.style-switcher .color li {
  margin-top: 8px;
  border: 1px solid #111;
}
.style-switcher .color li:first-child {
  margin-top: 0;
}
