@charset "UTF-8";
#CalendarMain {
  width: 310px;
  height: 402px;
  padding: 0;
  margin: 20px auto 20px;
  overflow: hidden;
  border-radius: 5px;
  border: 1px solid #f0f0f0; }

#title {
  width: 100%;
  height: 50px;
  background-color: #50d2c2;
  color: #fff; }

.selectBtn {
  font-weight: 900;
  font-size: 15px;
  color: #fff;
  cursor: pointer;
  text-decoration: none;
  padding: 17px 10px 6px 10px; }

/* .selectBtn:hover {
  background-color: #fff;
} */
.selectYear {
  float: left;
  margin-left: 50px;
  position: absolute; }

.selectMonth {
  float: left;
  margin-left: 120px;
  position: absolute; }

.month-1 {
  float: left;
  position: absolute; }

.nextMonth {
  float: right; }

.currentDay {
  float: right; }

#context {
  background-color: #fff;
  width: 310px; }

.week-1 {
  width: 310px;
  height: 40px; }

.week-1 > h3 {
  width: 44px;
  float: left;
  color: #808080;
  text-align: center;
  margin: 0;
  padding: 0;
  margin-top: 20px;
  font-size: 16px;
  font-weight: bold; }

.dayItem {
  float: left;
  overflow: hidden; }

.lastItem {
  visibility: hidden;
  color: #f0f0f0; }

.item {
  width: 42px;
  color: #333;
  float: left;
  text-align: center;
  cursor: pointer;
  margin: 0;
  font-family: "微软雅黑";
  font-size: 13px;
  position: relative; }

.item i {
  position: absolute;
  bottom: 10px;
  left: 20px;
  display: block;
  width: 5px;
  height: 5px; }

.item i.blue {
  background: url(../images/blue-dian.png) no-repeat; }

.item i.white {
  background: url(../images/white-dian.png) no-repeat; }

.item i.red {
  background: url(../images/red-dian.png) no-repeat; }

.item:hover {
  color: #b9121b; }

.currentItem > a {
  background-color: #50d2c2;
  width: 35px;
  line-height: 45px;
  display: inline-block;
  /* -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  border-radius: 50%; */
  color: #fff; }

.currentItem i.blue {
  background: url(../images/white-dian.png) no-repeat; }

.currentItem i.red {
  background: url(../images/white-dian.png) no-repeat; }

.item.nowitem:hover > i {
  /*background: url(../images/white-dian.png) no-repeat;*/ }

.item.nowitem > a {
  width: 35px;
  line-height: 45px;
  display: inline-block;
  margin: 5px auto; }

.item.nowitem:hover > a {
  background-color: #50d2c2;
  color: #fff; }

#Container {
  overflow: hidden;
  float: left; }

#center {
  width: 100%;
  overflow: hidden; }

#centerMain {
  width: 300%;
  margin-left: -100%; }

#selectYearDiv {
  float: left;
  background-color: #fff; }

#selectYearDiv > div {
  float: left;
  text-align: center;
  font-family: "微软雅黑";
  font-size: 16px;
  border: 1px solid #ccc;
  margin-left: -1px;
  margin-top: -1px;
  cursor: pointer;
  color: #909090; }

.currentYearSd, .currentMontSd {
  color: #ff4400 !important; }

#selectMonthDiv {
  float: left;
  background-color: #fff; }

#selectMonthDiv > div {
  color: #909090;
  float: left;
  text-align: center;
  font-family: "微软雅黑";
  font-size: 16px;
  border: 1px solid #ccc;
  margin-left: -1px;
  margin-top: -1px;
  cursor: pointer; }

#selectYearDiv > div:hover, #selectMonthDiv > div:hover {
  background-color: #efefef; }

#centerCalendarMain {
  float: left; }

.clearfix {
  *zoom: 1; }

.clearfix:before,
.clearfix:after {
  display: table;
  content: ""; }

.clearfix:after {
  clear: both; }

